}

.mainNavElm{
  color: black;
  position: relative;
  padding: 10px;
  cursor: pointer;
  font-size: 25px;
  z-index: 1;
  transition: 0.8s;
}

.mainNavElmSelected{
  position: relative;
  color: black;
  position: relative;
  padding: 10px;
  cursor: pointer;
  font-size: 25px;
  z-index: 1;
  transition: 0.8s;
  overflow: hidden;
}

.mainNavElmSelected::before{
  position: absolute;
  content: "";
  width: 100%;
  top: 0%;
  bottom: 0%;
  left: 0;
  background-image: linear-gradient(to bottom, rgba(255,255,255,1) 0%, rgba(235,99,99,1) 50%, rgba(244,49,49,1) 100%);
  z-index: -1;
  -webkit-animation: risingGradient 1s;
          animation: risingGradient 1s;
}

@-webkit-keyframes risingGradient {
  0%{
    top: 100%;
  }
  100%{
    top: 0%;
  }
}

@keyframes risingGradient {
  0%{
    top: 100%;
  }
  100%{
    top: 0%;
  }
}

.mainNavLink{
